/* bootstrap breadcrumbs*/
.breadcrumb {
  margin: 0;
  padding: 0;
  background-color: transparent;
  border-radius: 0;
  color: inherit;
  font-size: 14px;
  font-weight: 400;
  font-family: "Oswald", sans-serif;
  letter-spacing: 0.1em;
  line-height: 32px;
  text-transform: uppercase;
}
