/* 
** toTop
*/
#toTop {
  bottom: 0;
  right: 0;
  display: none;
  width: 60px;
  height: 60px;
  overflow: hidden;
  position: fixed;
  text-decoration: none;
  z-index: 1000;
  font-size: 0;
  color: transparent;
  background-color: #e43d4e;
  opacity: 0.7;
}

#toTop:after {
  color: #ffffff;
  content: "\f106";
  font-size: 16px;
  line-height: 60px;
  font-family: "FontAwesome";
  text-align: center;
  display: block;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  transition: all 0.2s linear;
}

#toTopHover, #toTop:hover {
  bottom: 3px;
  opacity: 1;
}

#toTop:hover:after {
  color: #ffffff;
}

#toTop:active, #toTop:focus {
  outline: medium none;
}

.modal-open #toTop {
  visibility: hidden;
}

/* Countdown */
#comingsoon-countdown {
  text-align: center;
}

#comingsoon-countdown:before,
#comingsoon-countdown:after {
  clear: both;
  display: table;
  content: " ";
}

.countdown-rtl {
  direction: rtl;
}

.countdown-row {
  clear: both;
  display: table;
  margin: auto;
  padding: 20px 0;
  text-align: center;
}

.countdown-section {
  display: table-cell;
  padding: 10px 30px 20px;
  text-align: center;
  border-left: 1px solid rgba(255, 255, 255, 0.15);
}
.countdown-section:first-child {
  border-left-width: 0;
}

.countdown-amount {
  font-size: 80px;
  font-family: "Oswald", sans-serif;
  line-height: 1;
  font-weight: 700;
  color: #e43d4e;
}

.countdown-period {
  display: block;
  color: #ffffff;
  font-size: 12px;
  text-transform: uppercase;
  letter-spacing: 0.2em;
  font-weight: 700;
  min-width: 11em;
}

.countdown-descr {
  display: block;
}

@media (max-width: 767px) {
  .countdown-section {
    padding: 20px 8px 0;
  }
  .countdown-amount {
    font-size: 40px;
  }
  .countdown-period {
    min-width: 4em;
    letter-spacing: 0.1em;
  }
}
