}

.gradientradial-background {
  overflow: hidden;
}

.s-parallax > *,
.s-overlay > *,
.s-muted > *,
.s-overlay-diagonal-double > *,
.pattern-background > *,
.gradientradial-background > *,
.gradient-background > *,
.gradientvertical-background > *,
.gradientdarken-background > * {
  z-index: 4;
  position: relative;
}

.s-parallax:before,
.s-overlay:before,
.s-muted:before,
.s-overlay-diagonal-double:before,
.s-overlay-diagonal-double:after,
.pattern-background:before,
.gradient-background:before,
.gradientvertical-background:before,
.gradientdarken-background:before {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.s-muted:before,
.s-parallax:before {
  opacity: 0.8;
}

.s-overlay-diagonal {
  overflow: hidden;
  padding: 10px 0;
  background-clip: content-box;
}
.s-overlay-diagonal:before {
  width: 40%;
  height: 3000px;
  margin-left: 30%;
  top: 50%;
  left: 0;
  right: auto;
  bottom: auto;
  transform: translateY(-50%) rotate(-45deg);
}
@media (max-width: 1200px) {
  .s-overlay-diagonal:before {
    width: 70%;
    margin-left: 15%;
  }
}

.s-overlay-diagonal-double {
  overflow: hidden;
  padding: 10px 0;
}
.s-overlay-diagonal-double:before {
  background: linear-gradient(45deg, #e43d4e 30%, transparent 30%);
  opacity: 0.6;
}
.s-overlay-diagonal-double:after {
  background: linear-gradient(45deg, transparent 70%, #e43d4e 70%);
  opacity: 0.6;
}

.gradient-background:before {
  background: #e43d4e;
  background: -moz-linear-gradient(left, #e43d4e 0%, #37c7ef 100%);
  background: -webkit-gradient(linear, left top, right top, color-stop(0%, #e43d4e), color-stop(100%, #37c7ef));
  background: -webkit-linear-gradient(left, #e43d4e 0%, #37c7ef 100%);
  background: -o-linear-gradient(left, #e43d4e 0%, #37c7ef 100%);
  background: -ms-linear-gradient(left, #e43d4e 0%, #37c7ef 100%);
  background: linear-gradient(to right, #e43d4e 0%, #37c7ef 100%);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="$colorMain", endColorstr="$colorMain2", GradientType=1);
}

.gradientvertical-background:before {
  background: #e43d4e; /* Old browsers */
  background: -moz-linear-gradient(top, #e43d4e 0%, #37c7ef 100%);
  background: -webkit-linear-gradient(top, #e43d4e 0%, #37c7ef 100%);
  background: -o-linear-gradient(top, #e43d4e 0%, #37c7ef 100%);
  background: -ms-linear-gradient(top, #e43d4e 0%, #37c7ef 100%);
  background: linear-gradient(to bottom, #e43d4e 0%, #37c7ef 100%);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="$colorMain", endColorstr="$colorMain2", GradientType=0);
}

.gradientdarken-background:before {
  background: -moz-linear-gradient(top, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.4) 100%);
  background: -webkit-linear-gradient(top, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.4) 100%);
  background: -o-linear-gradient(top, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.4) 100%);
  background: -ms-linear-gradient(top, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.4) 100%);
  background: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.4) 100%);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#00000000", endColorstr="#00000000", GradientType=0);
}

.gradientradial-background:before {
  content: "";
  display: block;
  position: absolute;
  width: 1400px;
  height: 1400px;
  top: 50%;
  left: 50%;
  margin-top: -700px;
  margin-left: -700px;
  z-index: 2;
  background: -moz-radial-gradient(center, ellipse cover, rgba(255, 255, 255, 0.24) 0%, rgba(255, 255, 255, 0) 50%); /* FF3.6+ */
  background: -webkit-gradient(radial, center center, 0, center center, 50%, color-stop(0%, rgba(255, 255, 255, 0.24)), color-stop(50%, rgba(255, 255, 255, 0))); /* Chrome,Safari4+ */
  background: -webkit-radial-gradient(center, ellipse cover, rgba(255, 255, 255, 0.24) 0%, rgba(255, 255, 255, 0) 50%); /* Chrome10+,Safari5.1+ */
  background: -o-radial-gradient(center, ellipse cover, rgba(255, 255, 255, 0.24) 0%, rgba(255, 255, 255, 0) 50%); /* Opera 12+ */
  background: -ms-radial-gradient(center, ellipse cover, rgba(255, 255, 255, 0.24) 0%, rgba(255, 255, 255, 0) 50%); /* IE10+ */
  background: radial-gradient(ellipse at center, rgba(255, 255, 255, 0.24) 0%, rgba(255, 255, 255, 0) 50%); /* W3C */
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#33ffffff", endColorstr="#00ffffff", GradientType=1); /* IE6-9 fallback on horizontal gradient */
}

.s-overlay.gradientradial-background:before {
  width: 100vw;
  height: 100vw;
  margin-top: -50vw;
  margin-left: -50vw;
}

